import request from '../HttpRequest'
import map from 'lodash/map'

function UserClass() {
  function prepareUserRoles(user) {
    user.authorities = map(user.authorities, 'authority');
    return user;
  }

  this.getCurrentUser = function () {
    return request.get('/users/user')
      .then((user) => {
        if (!user || user === 'USER_IS_NOT_AUTHORIZED') {
          return Promise.reject();
        } else {
          return prepareUserRoles(user);
        }
      })
      .catch((e) => {
        e && console.warn(e);
        return Promise.reject(new Error('User is not logged in'));
      });
  }

  this.login = function (login, password) {
    return request.post('/authenticate/login', {
      username: login,
      password: password
    })
      .then(prepareUserRoles);
  }

  this.logout = function () {
    return request.post('/authenticate/logout');
  }
}

export default UserClass



